@charset "UTF-8";
html,
body {
  min-height: 100%;
  font-family: sans-serif;
  scroll-behavior: smooth;
  background-color: #000; /* фон для всего */
  color: #fff;
}

body {
  display: flex;
  justify-content: center;
}

main {
  width: 100%;
}

.wrapper {
  display: flex;
  width: 100%;
  max-width: 1440px;
  flex-direction: column;
  align-items: center;
}

[class*=__container] {
  padding-block: 120px;
  padding-inline: 90px;
}
@media (max-width: 1025px) {
  [class*=__container] {
    padding-block: 100px;
  }
}
@media (max-width: 768px) {
  [class*=__container] {
    padding-block: 45px;
    padding-inline: 32px;
  }
}